import Vector from './utils/vector';

export default class Pointer {
  constructor({ x = 0, y = 0 }) {
    this.position = new Vector({ x, y });
    this.velocity = new Vector().randomAngle().setMag(1);
    // this.velocity = new Vector().setAngle(0).setMag(1);

    this.rotateAmount = Math.PI * 0.01;
    this.currentRotate = this.rotateAmount;

    this.leneanceX = 100;
    this.leneanceY = 20;
  }

  _outOfBounds({ x, y }, width, height) {
    return x < 0 || y < 0 || x >= width || y >= height;
  }

  _wrapPosition(width, height) {
    let { x, y } = this.position;
    if (x < -this.leneanceX) {
      this.position.x = width + this.leneanceX;
    }

    if (y < -this.leneanceY) {
      this.position.y = height + this.leneanceY;
    }

    if (x > width + this.leneanceX) {
      this.position.x = -this.leneanceX;
    }

    if (y > height + this.leneanceY) {
      this.position.y = -this.leneanceY;
    }
  }

  update(frame, width, height) {
    if (frame % 30 == 0) {
      let rd = Math.random();
      if (rd > 0.6) {
        if (rd < 0.8) {
          this.currentRotate = this.rotateAmount
        } else {
          this.currentRotate = -this.rotateAmount;
        }
      } else if (rd > 0.4) {
        this.currentRotate = 0;
      }
    }
    this.velocity.rotate(this.currentRotate);

    this.position.add(this.velocity);

    this._wrapPosition(width, height);
  }

  render() {
    ctx.save();
    ctx.beginPath();
    ctx.arc(this.position.x, this.position.y, 3, 0, Math.PI * 2);
    ctx.fill();

    let x = radius => (Math.cos(this.velocity.getAngle()) * radius) + this.position.x;
    let y = radius => (Math.sin(this.velocity.getAngle()) * radius) + this.position.y;

    ctx.beginPath();
    ctx.moveTo(x(1), y(1));
    ctx.lineTo(x(10), y(10));
    ctx.stroke();
    ctx.restore();
  }
}
