import Engine from './utils/engine';
import { getInterpolatedColours, getClosestColour, getClosestColourIndex } from './utils/utils';

import Pointer from './Pointer';

// import Engine from './utils/engine3d';

const WIDTH = 480;
const HEIGHT = 480;

const LEN = 100;
const WID = 2;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const colourArr = getInterpolatedColours([
  { r: 241, g: 100, b: 92},
  { r: 148, g: 147, b: 242},
  { r: 93, g: 213, b: 247},
  { r: 241, g: 100, b: 92},
], 250);

let grads;

// const brushes = new Array(5).fill(1).map(() => new Pointer({ x: WIDTH * Math.random(), y: HEIGHT * Math.random() }));
// REMIX
const brushes = new Array(1).fill(1).map(() => new Pointer({ x: WIDTH * Math.random(), y: HEIGHT * Math.random() }));

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

grads = colourArr.map((c, index) => {
  const grad = ctx.createLinearGradient(0, 0, LEN, 0);
  grad.addColorStop(0, `rgb(${ c.r }, ${ c.g }, ${ c.b })`);
  grad.addColorStop(1, `rgb(${ c.r - 100 }, ${ c.g - 100 }, ${ c.b - 100 })`);

  return grad;
});

app.onRender(({ frame, width, height, debug }) => {
  // ctx.clearRect(0, 0, width, height);

  for (let i = 0; i < brushes.length; i++) {
    brushes[i].update(frame, width, height);
    let t = (brushes[i].velocity.getAngle() + Math.PI) / (Math.PI * 2);

    // let colour = getClosestColour(t, colourArr);
    // ctx.fillStyle = `rgb(${ colour.r }, ${ colour.g }, ${ colour.b })`
    // ctx.beginPath();
    // ctx.arc(brushes[i].position.x, brushes[i].position.y, 10, 0, Math.PI * 2);
    // ctx.fill();

    // REMIX
    ctx.save();
    ctx.translate(brushes[i].position.x, brushes[i].position.y);

    let colour = getClosestColourIndex(t, colourArr);
    // ctx.fillStyle = `rgb(${ colour.r }, ${ colour.g }, ${ colour.b })`;
    ctx.fillStyle = grads[colour];
    ctx.fillRect(0, 0, LEN, WID);

    ctx.restore();
  }
});

app.start();
